import React, { Component } from 'react';
import { List ,Tabs, Badge} from 'antd-mobile';
import {goods} from '../../data/goods';
import noImg from '../../assets/goods/noImg.svg';



import './index.css'
const Item = List.Item;
const tabs = [
    { title: <Badge>商品详情</Badge> },
    { title: <Badge>商品规格</Badge> },
  ];
  

export class index extends Component {
    constructor(props){
        super(props);
        this.state={
            goodDetails:"",
        }
    }
    componentDidMount(){
        let id=this.props.match.params.id;
        let goodDetails=[];
        for(let i=0;i<goods.length;i++){
            if(goods[i].id==id){
                goodDetails=(goods[i])
            }
        }
        this.setState({goodDetails:goodDetails})

    }
    render() {
        let {goodDetails}=this.state;
        // console.log(goodDetails)
        
        return (
            <div>
                <div className='goodsDetails'>
                    <div>
                        {
                            goodDetails.thumbnail===""?
                                <img src={noImg} style={{ width: '385px', height: '230px' }} alt="商品" />
                                :
                                <img src={goodDetails.thumbnail} alt="商品" />
                        }
                    </div>
                    <div className="description">
                        <p>{goodDetails.descrip} </p>
                        <p>￥<span className="price"> {goodDetails.sale_price}</span> </p>
                        <p>
                            <span>运费：免运费</span>
                            <span>库存：{goodDetails.stock_count}</span>
                            <span>销量：{goodDetails.sale_count}</span>
                        </p>
                    </div>
                </div>
                <List renderHeader={() => ''} renderFooter={() => ''} className="goodsDetails_ways">
                    <Item arrow="horizontal" >配送方式： 快递、自提</Item>
                </List>
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '250px', backgroundColor: '#fff' }}>
                        暂无
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '250px', backgroundColor: '#fff' }}>
                        暂无
                    </div>
                </Tabs>
                <div className="goodsDetails_go">
                    <span>加入购物车</span>
                    <span>立即购买</span>
                </div>
            </div>
        )
    }
}

export default index