import React from 'react';

import { Carousel } from 'antd-mobile';

class AppCarousel extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: [
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1560596286473&di=34eb52836a019d9ded01442f1d3ce79e&imgtype=0&src=http%3A%2F%2Fn.sinaimg.cn%2Ftravel%2Fcrawl%2F114%2Fw550h364%2F20180404%2F-VoT-fysuuya4936234.jpg', 
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1561191121&di=dcd0ac8af824488ec59c2e05751b78dc&imgtype=jpg&er=1&src=http%3A%2F%2Fimage.9928.tv%2FMobile%2F54110%2F20170518%2F20170518192244532.JPG', 
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1560597751044&di=2e6a92396c6c451b3fcbfab75b5f1a8f&imgtype=0&src=http%3A%2F%2Fs13.sinaimg.cn%2Fmw690%2Fb8b90939t7c1891f64a9c%26690'
        ],
      });
    }, 100);
  }
  render() {
    return (
        <Carousel
          autoplay={true}
          infinite
          // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          // afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={val}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
    );
  }
}

export default AppCarousel;