import React, { Component } from 'react'
import { Grid } from 'antd-mobile';
import {goods} from '../../data/goods';
import {classifyName} from '../../data/classify';
import noImg from '../../assets/goods/noImg.svg';


const demoImgArr = ["烟" , "酒" , "零食","饮料","粮油","调味品","生活用品","五金工具"]
const ticks = demoImgArr.map(item => require("../../assets/classification/" + item + ".svg"))

export class index extends Component {
    constructor(props){
        super(props);
        this.state={
            classGoods:[],
        }
    }
    componentDidMount(){
        let id=this.props.match.params.id;
        let classGoods=[];
        console.log(this.props.match.params.id)
        for(let i=0;i<goods.length;i++){
            if(goods[i].classify==id){
                classGoods.push(goods[i])
            }
        }
        this.setState({classGoods:classGoods})

    }
    
    
    handelClick=(e)=>{
        // this.props.history.push('/');
        // console.log('this.props', this.props)
        console.log('商品id', e.target.dataset.id)
        let id=e.target.dataset.id;
        if(id!==undefined){
            this.props.history.push(`/goodsdetails/${id}`)
        }

    }

    render() {
        let classNum=this.props.match.params.id;
        let {classGoods}=this.state;
        
        return (
            <div>
                <div className="sub-title">{classifyName[classNum]}</div>
                <Grid data={classGoods}
                columnNum={2}
                renderItem={dataItem => (
                    <div style={{ padding:'12.5px' }} data-id={dataItem.id} onClick={this.handelClick}>
                        {
                            dataItem.icon===""?
                                <img  data-id={dataItem.id} src={noImg} style={{ width: '85px', height: '85px' }} alt="商品" />
                                :
                                <img  data-id={dataItem.id} src={dataItem.icon} style={{ width: '85px', height: '85px' }} alt="商品" />
                            }
                        <div style={{ color: '#888', fontSize: '14px', marginTop: '12px' }}>
                            <span style={{textAlign:"left",display: "inline-block",width: "100%"}}>{dataItem.text} {dataItem.format} /{dataItem.unit} </span>
                            <p style={{textAlign:'left',color: '#e4393c'}}>
                                <span style={{width:"50%",display: "inline-block"}}>￥ {dataItem.sale_price}  </span>
                                {/* <span style={{width:"50%",display: "inline-block",textAlign:'right'}}>购买</span> */}
                            </p>
                        </div>
                    </div>
                )}
                />
            </div>
        )
    }
}

export default index






