import React from 'react'
import { TabBar } from 'antd-mobile';
import NavBar from '../NavBar';
import AppCarousel from '../Carousel';
import HomeGoodsGrid from '../HomeGoodsGrid';
import ShoppingCartGoodGrid from '../ShoppingCartGoodGrid';
import OrderGoodGrid from '../OrderGoodGrid';
import PersonalCenterGrid from '../PersonalCenterGrid';
import Tips from '../Tips';


class index extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: '/',
      hidden: false,
    };
  }
  componentDidMount(){
  }
  handelTouchShow=()=>{
    this.setState({
        hidden: !this.state.hidden,
    });
  }

  renderHomeContent(pageText) {
    return (
      <div style={{ backgroundColor: 'white', height: '100%'}} >
        <NavBar {...this.props}></NavBar>
        <AppCarousel/>
        <HomeGoodsGrid {...this.props}/>
      </div>
    );
  }
  renderShoppingCartContent(pageText) {
    return (
      <div style={{height: '100%'}} >
        <NavBar {...this.props}></NavBar>
        {/* <ShoppingCartGoodGrid {...this.props}></ShoppingCartGoodGrid> */}
        <Tips></Tips>
      </div>
    );
  }
  renderOrderContent(pageText) {
    return (
      <div style={{height: '100%'}} >
        <NavBar {...this.props}></NavBar>
        {/* <OrderGoodGrid {...this.props}></OrderGoodGrid> */}
        <Tips></Tips>
      </div>
    );
  }
  renderPersonalcenter(pageText) {
    return (
      <div style={{height: '100%'}} >
        <NavBar {...this.props}></NavBar>
        <PersonalCenterGrid {...this.props}></PersonalCenterGrid>
      </div>
    );
  }

  render() {
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          hidden={this.state.hidden}
        >
          <TabBar.Item
            title="首页"
            key="Life"
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(https://zos.alipayobjects.com/rmsportal/sifuoDUQdAFKAVcFGROC.svg) center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(https://zos.alipayobjects.com/rmsportal/iSrlOTqrKddqbOmlvUfq.svg) center center /  21px 21px no-repeat' }}
            />
            }
            selected={this.props.match.path === '/'}
            onPress={() => {
              this.setState({
                selectedTab: '/',
              });
              this.props.history.push('/');

            }}
            data-seed="logId"
          >
            {this.renderHomeContent('home')}
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(https://gw.alipayobjects.com/zos/rmsportal/BTSsmHkPsQSPTktcXyTV.svg) center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(https://gw.alipayobjects.com/zos/rmsportal/ekLecvKBnRazVLXbWOnE.svg) center center /  21px 21px no-repeat' }}
              />
            }
            title="购物车"
            key="Koubei"
            // badge={'1'}
            dot
            selected={this.props.match.path === '/shoppingcart'}
            onPress={() => {
              this.setState({
                selectedTab: 'shoppingcart',
              });
              this.props.history.push('/shoppingcart');

            }}
            data-seed="logId1"
          >
            {this.renderShoppingCartContent('shoppingcart')}
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(https://zos.alipayobjects.com/rmsportal/psUFoAMjkCcjqtUCNPxB.svg) center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(https://zos.alipayobjects.com/rmsportal/IIRLrXXrFAhXVdhMWgUI.svg) center center /  21px 21px no-repeat' }}
              />
            }
            title="订单"
            key="Friend"
            dot
            selected={this.props.match.path === '/order'}
            onPress={() => {
              this.setState({
                selectedTab: '/order',
              });
              this.props.history.push('/order');
            }}
          >
            {this.renderOrderContent('order')}
          </TabBar.Item>
          <TabBar.Item
            icon={{ uri: 'https://zos.alipayobjects.com/rmsportal/asJMfBrNqpMMlVpeInPQ.svg' }}
            selectedIcon={{ uri: 'https://zos.alipayobjects.com/rmsportal/gjpzzcrPMkhfEqgbYvmN.svg' }}
            title="我的"
            key="my"
            selected={this.props.match.path === '/personalcenter'}
            onPress={() => {
              this.setState({
                selectedTab: '/personalcenter',
              });
              this.props.history.push('/personalcenter');
            }}
          >
            {this.renderPersonalcenter('personalcnter')}
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default index